import React from "react";
import {InternalLink} from "../framework/Links";
import {RouteID} from "../application/Routes";
import "../framework/site.scss";

export default class ContactUsSection extends React.PureComponent {
  render() {
    return (
        <section className="cta">
            <InternalLink routeID={RouteID.ROUTE_ID_CONTACT} type="primary accent button">Contact us</InternalLink>
        </section>
    );
  }
}