import React from "react";
import "../framework/site.scss";

export interface ImprovementSectionProps {
    showHeader: boolean
}

export default class ImprovementContentSection extends React.PureComponent<ImprovementSectionProps> {
  render() {
    return (
        <section className="content">
            {this.props.showHeader ? <h5>Improvement</h5> : <></>}
            <p>
                Technology evolves rapidly. What has worked for many years no longer reflects today's realities and demand.
            </p>
            <p>
                We help transform, modernise and improve custom built software solutions. We believe in a better software every day.
            </p>
            <p>
                Over the course of decades we were practising Agile software development methodologies,
                applying community-driven principles of software engineering and utilising comprehensive testing
                methods at different levels to ensure high quality.
            </p>
            <p>
                Our key rule is to leave project in a better shape today than it was yesterday.
                This rule is a core of our continuous improvement policy.
            </p>
            <p>
                Talk to us about your challenges and how we can help improve your product or service.
            </p>
        </section>
    );
  }
}