import React from "react";
// import {InternalLink} from "../framework/Links";
// import {RouteID} from "../application/Routes";
import LogoMPower from "../assets/logo-mpower.png";
import "../framework/site.scss";

export default class MPowerMSLFeatureSection extends React.PureComponent {
  render() {
    return (
        <section className="featured project">
            <img src={LogoMPower} alt="MPower MSL logo"/>
            <div>
                <p>MPower MSL provides the world's most iconic sports brands, hospitality venues, and golf clubs with leading Analytics, Membership and Point of Sale software.</p>
                <p>We were engaged to perform multiple modernisations and transformations for a various projects.</p>
                {/*<p><InternalLink routeID={RouteID.ROUTE_ID_MPOWER_MSL}>Read the story</InternalLink></p>*/}
            </div>
        </section>
    );
  }
}