import React from "react";
import ContactUsSection from "../sections/ContactUsSection";
import ImprovementBannerSection from "../sections/ImprovementBannerSection";
import ImprovementContentSection from "../sections/ImprovementContentSection";
import MPowerMSLFeatureSection from "../sections/MPowerMSLFeatureSection";
import "../framework/site.scss";

export default class ImprovementContent extends React.PureComponent {
  render() {
    return (
      <main>
          <ImprovementBannerSection />
          <ImprovementContentSection showHeader={false} />
          <ContactUsSection />
          <MPowerMSLFeatureSection />
      </main>
    );
  }
}