import React from "react";
import jumbo from "../assets/jumbo-improvement.jpg";
import "../framework/site.scss";

export default class ImprovementBannerSection extends React.PureComponent {
  render() {
      const rootStyle = {
          backgroundImage: 'url(' + jumbo + ')'
      };

      return (
          <section className="intro" style={rootStyle}>
              <h1>
                  Improvement
              </h1>
          </section>
    );
  }
}